import React, { useEffect } from "react";
import WebSource from "./Source";
import Nav from "./Nav";

// AOS AND COMPOENNTS
import Aos from "aos";
import "aos/dist/aos.css";

function WebSources() {
  // AOS INIT
  useEffect(() => {
    Aos.init({ duration: 1000 });
  }, []);

  useEffect(() => {
    document.title = "Web Resources | Project Atlas";
  });
  return (
    <div>
      <Nav />
      {/* SCROLL UP */}
      <section className="webdev elegant-color-dark text-white">
        <div className="container">
          <div className="devInfo text-center">
            <span className="badge purple accent-4  pl-3 pr-3 p-2 badge-pill">
              Resources
            </span>
            <h2>Web Development Resources</h2>
          </div>
        </div>

        <div className="container mx-auto">
          <div className="row">
            {WebSource.map(function (item, index) {
              return (
                <div
                  class="card blue-grey darken-4 mb-3 text-white"
                  style={{ maxWidth: "550px" }}
                  data-aos="fade-up"
                >
                  <div class="row ">
                    <div class="col-md-4">
                      <div className="view zoom">
                        <img
                          src={item.img}
                          alt={item.imgAlt}
                          class="img-fluid"
                        />
                      </div>
                    </div>
                    <div class="col-md-8">
                      <div class="card-body">
                        <h3>{item.name}</h3>
                        <h6 className="badge blue-grey darken-2 badge-pill p-1">
                          {item.category}
                        </h6>
                        <p class="small">{item.desc}</p>
                        <a
                          className="btn unique-color rounded-pill"
                          href={item.link}
                        >
                          Visit
                        </a>
                      </div>
                    </div>
                  </div>
                </div>
              );
            })}
          </div>
          <h6 className="alert alert-warning">
            If you know more free hosting platforms that could be useful for
            other developers feel free to{" "}
            <a
              href="https://github.com/pruthvz/project-atlas"
              rel="noreferrer"
              target="_blank"
            >
              update this page.
            </a>
          </h6>
        </div>
      </section>
    </div>
  );
}

export default WebSources;
